@import "../base/mixins";
.blog{
    &__detail{
        padding: 60px 0;
        @media screen and (max-width: 767px) {
            padding: 30px 0;
        }
        &__tit{
            font-size: 32px;
            line-height: 1.5;
            text-transform: uppercase;
            @media screen and (max-width: 767px) {
                font-size: 26px;
            }
        }
        &__subtit{
            font-size: 28px;
            padding: 32px 0;
            text-transform: uppercase;
            display: block;
            width: 100%;
            @media screen and (max-width: 767px) {
                font-size: 22px;
            }
        }
        &__searchtit{
            border-top: 1px solid #bcbcbc;
            padding: 12px 0;
            display: block;
            h3{
                @include transition_default(0.3s);  
            }
            &:hover{
                h3{
                    color: #FDC254;
                }
            }
        }
        &__relateditem{
            padding: 8px 8px;
            display: block;
            &--tit{
                padding: 15px 0 0 0;
                font-size: 13px;
                line-height: 1.5;
            }
        }
        ul{
            padding-left: 16px;
        }
    }
    &__category{
        padding: 16px 0 32px 0;
        &__title{
            text-transform: uppercase;
        }
        &__sliders{
            position: relative;
            &.pc{
                @media screen and (max-width: 767px) {
                    display: none !important;
                }
            }
            &.mb{
                display: none !important;
                @media screen and (max-width: 767px) {
                    display: block !important;
                    .product__category__slideitem {
                        height: 620px;
                    }
                }
            }
        }
        &__slideitem{
            height: 520px;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            display: block;
        }
        .owl-nav{
            position: absolute;
            right: 5%;
            bottom: 10%;
            margin: 0 !important;
            @media screen and (max-width: 767px) {
                width: 100%;
                @include flex;
                box-sizing: border-box;
                @include justify-content('space-between');
                left: 0;
                right: 0;
                bottom: calc(50% - 22px);
            }
            .disabled{
                opacity: 1 !important;
            }
        }
        .owl-dots{
            position: absolute;
            left: 5%;
            bottom:10%;
            @media screen and (max-width: 767px) {
                width: 100%;
                left: 0;
                bottom: 0;
            }
            .owl-dot{
                outline: none;
            }
        }
        .owl-dot span{
            width: 12px !important;
            height: 12px !important;
        }
        .owl-dot span{
            outline: none !important;
            background: #fff !important;
        }
        .owl-dot.active span{
            background: #FDC254 !important;
        }
        button.owl-next, button.owl-prev{
            outline: none;
            padding: 0 !important;
            margin: 0 !important;
            width: 48px;
            height: 48px;
            border: 1px solid #fff !important;
            border-radius: 0 !important;
            background-color: #ffffff !important;
            background-size: 21px 14px !important;
            background-repeat: no-repeat !important; 
            background-position: center !important;
            &:hover{
                background-color: #FDC254 !important;
            }
            span{
                display: none;
            }
        }
        button.owl-next{
            background-image: url(../images/arrow-target-right-black.png)  !important;
            @media screen and (min-width: 768px) {
                border-left: 0 !important;
            } 
            &:hover{
                background-image: url(../images/arrow-target-right-white.png) !important;  
            }
        }
        button.owl-prev{
            background-image: url(../images/arrow-target-left-black.png)  !important;
            @media screen and (min-width: 768px) {
                border-right: 0 !important;
            }
            &:hover{
                background-image: url(../images/arrow-target-left-white.png) !important;  
            }
        }
        &__list{
            @include flex;
            @include flex-wrap;
            margin: 40px -0.5rem 0 -0.5rem;

        }
        &__item{
            width: 100%;
            padding: 16px;
            box-sizing: border-box;
            border: 1px solid #bfbfbf;
            margin: 0.7rem 0.5rem;
            @media screen and (min-width: 767px) {
                width: calc(50% - 1rem);
            }
            @media screen and (min-width: 992px) {
                width: calc(33.333% - 1rem);
            }
            @media screen and (min-width: 1199px) {
                width: calc(25% - 1rem);
            }
            &--tit{
                margin: 1.2rem 0 1rem 0;
                text-transform: uppercase;
                line-height: 1.5;
            }
            &--readmore{
                margin: 1rem auto 0 auto;
                display: table;
                border: 1px solid #bfbfbf;
                padding: 1rem;
                text-transform: uppercase;
                text-align: center;
                border-radius: 5px;
                font-weight: 600;
            }
            &--thumbbox{
                height: 12.5rem;
                position: relative;
                width: 100%;
                overflow: hidden;
                img{
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    min-width: 100%;
                    min-height: 100%;
                    @include transition_default(0.3s);
                    @include transform(translate(-50%,-50%));
                    
                }
            }
            display: block;
            
            &:hover{
                img{
                    opacity: 0.7;
                }
            }
        }
    }
}
